.meme-container {
  width: 100%;
  margin-top: 50px;
}
.meme-form {
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
}
.meme-form input {
  margin: 5px;
  padding: 5px 10px;
  color: #333;
  font-size: 1.5rem;
  font-weight: 600;
  flex: 1;
}
.meme-form button {
  flex: 1;
  margin: 5px;
  padding: 5px 10px;
  color: #333;
  font-weight: 400;
  font-size: 1.5rem;
  border: none;
  background: #fc3;
  cursor: pointer;
}
.memeImg {
  width: 90%;
  margin: 50px auto;
  position: relative;
  font-size: 2rem;
  font-weight: 600;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: #fff;
  -webkit-text-stroke: 2px black;
  letter-spacing: 1px;
}
.randomImg {
  width: 100%;
}
.top {
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
}
.bottom {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
}
@media only screen and (max-width: 786px) {
  .memeImg {
    font-size: 1.5rem;
    -webkit-text-stroke: 1.5px black;
  }
  .meme-form input {
    font-size: 1rem;
  }
}
@media only screen and (max-width: 420px) {
  .memeImg {
    font-size: 1rem;
    -webkit-text-stroke: 1px black;
  }
  .meme-form input {
    font-size: 0.8rem;
  }
}
